/*eslint-disable*/module.exports={messages:{"- **Anspruch**\n- Vermieter:innen haben die Pflicht, die Mietsache in einem Zustand\nzu übergeben und zu erhalten, die der vertraglichen Vereinbarung entspricht. \n- Dafür zahlen die Mietenden die Miete. \n- Wenn während der Mietzeit ein Mangel auftritt, dann muss die Vermieter:in ihn beseitigen. \n- **Ausnahme**: bei Kleinreparaturen bis zu einem Betrag von ca. 100 Euro pro Reparatur\n- wenn das im Vertrag so steht und\n- ein Jahres-Höchstbetrag festgelegt wurde. \n- **Durchsetzung:**\n- Den Anspruch auf Beseitigung des Mangels kann man gegenüber dem\nVermieter geltend machen, indem man ihn auffordert, den Mangel zu beseitigen. Oft geschieht dies gemeinsam\nmit der Mitteilung über den Mangel.":"- **tuyên bản**\n - Chủ nhà có nhiệm vụ giữ cho tài sản thuê trong một tiểu bang\nbàn giao và nhận được tương ứng với thỏa thuận hợp đồng. \n - Đó là những gì người thuê nhà trả tiền thuê nhà cho. \n - Nếu một khiếm khuyết xảy ra trong thời gian thuê nhà, thì chủ nhà phải sửa chữa nó. \n- **Ngoại trí**: để sửa chữa nhỏ lên đến một số tiền khoảng 100 euro cho mỗi sửa chữa\n - nếu nó nói như vậy trong hợp đồng và\n - một số tiền tối đa hàng năm đã được thiết lập. \n- **Thực thi: **\n - Yêu cầu bồi thường để loại bỏ các khiếm khuyết có thể được thực hiện chống lại\nYêu cầu chủ nhà bằng cách yêu cầu họ khắc phục lỗi. Điều này thường xảy ra với nhau\nvới thông báo về khiếm khuyết.","- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annulierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1500km)**, **400€ (bis zu 3500km)** oder **600€ (über 3500km)**.":"- Ein Anspruch auf **Ausgleichszahlung** ist das Recht auf eine **Entschädigung** durch Ihre Fluglinie.\n- Sie bekommen eine Ausgleichszahlung, wenn Sie Ihr Ziel wegen **Verspätung**, **Annulierung** oder **Überbuchung** nicht oder nur verspätet erreichen konnten.\n- Die **Höhe der Zahlung** richtet sich nach der **Flugstrecke** und beträgt **250€ (unter 1500km)**, **400€ (bis zu 3500km)** oder **600€ (über 3500km)**.","- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.":"- Yêu cầu bồi thường đối với khuyết tật khắc phục hậu hội là quyền yêu cầu chủ nhà của quý vị khắc phục hậu hội.\n- Chủ nhà có nhiệm vụ bàn giao và duy trì căn hộ thuê trong điều kiện có thể sử dụng được.\n- Nếu có lỗi xảy ra trong thời gian thuê nhà, chủ nhà phải loại bỏ nó.","- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mieter können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag mit einem Jahreshöchstbetrag festgelegt wurde.":"- Ein Anspruch auf **Mängelbeseitigung** ist das Recht, Ihre **Vermieter:in aufzufordern, den Mangel zu beseitigen**.\n- Vermieter:innen haben die Pflicht, die Mietwohnung in einem nutzbaren Zustand zu übergeben und zu erhalten.\n- Wenn während der Mietzeit ein Mangel auftritt, muss die Vermieter:in ihn beseitigen.\n- Ausnahme: Mieter können für Kleinreperaturen bis etwa 100 Euro veranwortlich sein, wenn das im Mietvertrag mit einem Jahreshöchstbetrag festgelegt wurde.","1. Ansprüche Prüfen":"1. Kiểm tra khiếu nại","2. Mögliche Ansprüche":"2. Các khiếu nại có thể","3. Nächste Schritte":"3. Các bước tiếp theo","Aufwendungsersatz":"Hoàn trả chi phí","Aus Ihren Angaben könnten sich folgende Ansprüche ergeben":"Các xác nhận quyền sở hữu sau đây có thể là do thông tin của bạn","Ausgleichszahlung":"Ausgleichszahlung","Außergerichtliche Einigung":"Außergerichtliche Einigung","Beratungsangebote":"Beratungsangebote","Beseitigung des Mangels durch die Vermieter:in":"Loại bỏ các khiếm khuyết của chủ nhà","Da für das gesamte Projekt nur zwölf Wochen zur Verfügung standen, konnte dieser Pfad leider noch nicht implementiert werden.":"Vì chỉ có mười hai tuần đã có sẵn cho toàn bộ dự án, con đường này vẫn chưa thể được thực hiện.","Die Justiz im Netz.":"Các cơ quan tư pháp trên mạng.","Diese Option ist leider noch nicht verfügbar.":"Thật không may, tùy chọn này chưa có sẵn.","Dieser Pfad wurde noch nicht implementiert":"Con đường này vẫn chưa được thực hiện","Dokumentation des Mangels":"Tài liệu về khiếm khuyết","Ein Anspruch ist das Recht, ein Verhalten von einer anderen Person zu fordern. Klicken Sie auf die Ansprüche, um mehr darüber zu erfahren.":"Yêu cầu bồi thường là quyền yêu cầu hành vi từ người khác. Nhấp vào xác nhận quyền sở hữu để tìm hiểu thêm về chúng.","Ein Anspruch ist das Recht, etwas von einer anderen Person oder einem Unternehmen zu fordern. Klicken Sie auf die Ansprüche, um mehr darüber zu erfahren.":"Yêu cầu bồi thường là quyền yêu cầu một cái gì đó từ một người hoặc công ty khác. Nhấp vào xác nhận quyền sở hữu để tìm hiểu thêm về chúng.","Einfach, online, jederzeit.":"Dễ dàng, trực tuyến, bất cứ lúc nào.","Erstberatung":"Erstberatung","Fluglinie kontaktieren":"Fluglinie kontaktieren","Gerichtsverfahren":"Gerichtsverfahren","Geschafft! Aus Ihren Angaben könnten sich folgende Ansprüche ergeben":"Xong! Các xác nhận quyền sở hữu sau đây có thể là do thông tin của bạn","Geschafft! Basierend auf den von Ihnen angegebenen Informationen, könnten die folgenden rechtlichen Ansprüche\nfür Sie in Frage kommen. Ein Anspruch ist das Recht ein bestimmtes Verhalten von einer anderen Person\neinzufordern.":"Xong! Dựa trên thông tin bạn cung cấp, các khiếu nại pháp lý sau đây có thể\nđi vào câu hỏi cho bạn. Yêu cầu bồi thường là quyền đối với một hành vi cụ thể của người khác\nđể yêu cầu.","Handlungsoptionen vergleichen":"So sánh các tùy chọn cho hành động","In welchem Bereich Ihres Lebens haben Sie ein Problem?":"Bạn có vấn đề trong lĩnh vực nào trong cuộc sống của bạn?","Info":"Thông tin","Ja":"Có","Jetzt starten!":"Bắt đầu ngay bây giờ","Lösungsfinder":"Công cụ tìm giải pháp","Mietminderung":"Giảm tiền thuê","Mängelanzeige bei Ihrer Vermieter:in":"Thông báo khuyết tật cho chủ nhà của bạn","Mögliche Ansprüche":"Mögliche Ansprüche","Nein":"Không","Nur kostenlose Angebote anzeigen":"Nur kostenlose Angebote anzeigen","Nächste Schritte":"Nächste Schritte","Online Klage einreichen":"Tộp đơn kiện trực tuyến","Problem einordnen":"Problem einordnen","Rechtlich beraten lassen":"Nhận tư vấn pháp lý","Rechtsproblem einordnen":"Phân loại vấn đề pháp lý","Schadensersatz":"Thiệt hại","Schlichtungsantrag stellen":"Schlichtungsantrag stellen","Unser Angebot":"Đề nghị của chúng tôi","Vermieter:in erneut kontaktieren":"Vermieter:in erneut kontaktieren","Weiter":"Tiếp","Wenn Sie sich über Ihre Ansprüche informiert haben, zeigen wir Ihnen, \nwie Sie diese durchsetzen können. Klicken Sie dafür einfach auf Weiter.":"Sau khi bạn đã được thông báo về các yêu cầu của mình, chúng tôi sẽ chỉ cho bạn \nlàm thế nào bạn có thể thực thi chúng. Chỉ cần nhấp vào Tiếp theo để làm như vậy.","Wie es funktioniert":"Cách hoạt động","Wobei benötigen Sie Unterstützung?":"Wobei benötigen Sie Unterstützung?","Wobei können wir Ihnen helfen?":"Chúng tôi có thể giúp gì cho bạn?","Zu den Beratungsangeboten":"Đi đến các dịch vụ tư vấn","Zum Prototypen":"Để nguyên mẫu","Zurück":"Quay lại","Zweites Schreiben an die Vermieter:in senden":"Thư thứ hai gửi chủ nhà vào","back":"Zurück","footer.disclaimer":"Trang này là một nguyên mẫu không đảm bảo sự chắc chắn về pháp lý!","footer.info":"Bạn có thể tìm thêm thông tin về trang này tại đây","footer.text":"Được xây dựng bởi ⚖️ và 🐌 với ❤️","forward":"Weiter","home.hero_offer":"Justizportal.de là một lời đề nghị của Bộ Tư pháp Liên bang và Bảo vệ người tiêu dùng hợp tác với các bộ tư pháp liên bang ở các tiểu bang liên bang. Cổng thông tin cung cấp cho bạn một cái nhìn tổng quan về quyền của bạn trong các tình huống hàng ngày khác nhau. Nó cũng giúp bạn phân loại và giải quyết các câu hỏi và vấn đề pháp lý của bạn. Bạn sẽ được thông báo về các lựa chọn hành động của bạn và sẽ được hỗ trợ trong việc khẳng định quyền của bạn.","home.howitwork_claims":"Sau đó, chúng tôi sẽ giúp bạn đánh giá những xác nhận quyền sở hữu nào có thể tồn tại trong trường hợp của bạn. Khiếu nại là quyền yêu cầu một hành động cụ thể từ người khác (ví dụ như thanh toán thiệt hại), để chịu đựng (ví dụ như cho thuê lại một căn hộ) hoặc để kiềm chế (ví dụ như nhập vào một tài sản). invidunt ut labore.","home.howitwork_classify":"Trước tiên, chúng tôi sẽ giúp bạn làm rõ liệu có vấn đề pháp lý trong trường hợp của bạn hay không. Chúng tôi sẽ cho bạn thấy thông tin phù hợp với trường hợp của bạn.","home.howitwork_heading":"Cách hoạt động","home.howitwork_lawsuit":"Khi tất cả các lựa chọn khác đã hết, chúng tôi sẽ giúp bạn nộp đơn khiếu nại với tòa án quận trực tuyến. Để đạt được điều này, chúng tôi giải thích quá trình của quá trình trực tuyến của chúng tôi cho bạn và cung cấp thông tin minh bạch về các chi phí của thủ tục tố tụng pháp lý cũng như các tư vấn pháp lý có thể.","home.howitwork_options":"Dựa trên điều này, chúng tôi sẽ đề xuất các giải pháp có thể <0> cho bạn - ban đầu cho một giải quyết tranh chấp thân thiện với phía đối diện. </0>","home.howitwork_subheading":"Chúng tôi có thể giúp bạn giải quyết vấn đề pháp lý của mình bằng bốn bước đơn giản:","home.howtohelp_footer":"Bạn có thể tìm hiểu thêm về các vấn đề pháp lý mà chúng tôi có thể giúp bạn trên các trang sau đây.","home.sub_heading":"Chúng tôi thông báo cho bạn về quyền lợi của bạn trong cuộc sống hàng ngày và giúp bạn giải quyết vấn đề pháp lý của bạn trong các bước đơn giản","nextsteps.header":"Các bước tiếp theo có thể của bạn để giải quyết vấn đề","nextsteps.sub_header":"Chúng tôi đã tóm tắt các bước có thể để bạn thực thi khiếu nại của mình tại đây. Nhấp vào từng hộp một để tìm hiểu thêm và nhận được hỗ trợ.","Über Ansprüche informieren":"Thông báo về khiếu nại","Über das Justizportal eine Klage einreichen":"Nộp đơn yêu cầu bồi thường qua cổng thông tin tư pháp"}};